import Vue from 'vue'
import router from './router'
import store from './store'
import moment from 'moment'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import VueLazyLoad from 'vue-lazyload'
import 'vue2-animate/dist/vue2-animate.min.css'
import 'font-awesome/css/font-awesome.css'
import '@/assets/css/other.css'
import '@/assets/icon/iconfont.css'
import noImg from '@/assets/img/no-image.jpg'
import VueTouch from 'vue-touch'
import App from './App'

const IMG_SCALE = 1.2
const RESIZE_CLASS = 'resize'
Vue.use(VueLazyLoad, {
adapter: {
loaded ({ bindType, el, naturalHeight, naturalWidth, $parent, src, loading, error, Init }) {
if (el.className.indexOf(RESIZE_CLASS) > -1 && el.clientHeight / el.clientWidth > IMG_SCALE) {



}
}
},
error: noImg

})

Vue.use(LoadingPlugin)
Vue.use(ToastPlugin)
Vue.use(ConfirmPlugin)
Vue.use(VueTouch, {name: 'v-touch'})

moment.locale('zh-cn')

const FastClick = require('fastclick')
FastClick.attach(document.body)

Vue.prototype.moment = moment
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
template: '<App/>',
components: { App }
})



