import Slider from '@/components/slider/slider'
import Scroll from '@/components/scroll/scroll'
import { isIphoneX } from '@/assets/js/brower'
import animations from 'create-keyframe-animation'
import axios from '@/api/axiosApi'
import homeList from '@/assets/json/homeList.json'
import bannerList from '@/assets/json/bannerAddress.json'
import AITag from '@/assets/img/AI_tag.png'
import icon from '@/assets/json/homeIcon.json'

// 每一个的Area的高度，都是一样的
const AreaItemHeight = 138
const MarginBottom = 15
let TopHeight = 223
const BottomHeight = 50

export default {
  name: 'Home',
  data () {
    return {
      startIndex: 0,
      endIndex: 3,
      bannerList: bannerList,
      homeList,
      AITag,
      icon
    }
  },
  components: {
    Slider, Scroll
  },
  created () {
    this.probeType = 3
    this.listenScroll = true
    this.sliderHeight = 210 + 20
    this.paddingBottom = 0
    if (isIphoneX()) {
      this.sliderHeight += 14
      this.paddingBottom = 44
    }
    TopHeight = this.sliderHeight - 45
    this._getBanner()
  },
  mounted () {
    this.endIndex = Math.floor((window.innerHeight - TopHeight - BottomHeight) / (AreaItemHeight + MarginBottom))
  },
  methods: {
    _getBanner () {
      axios.get(this, '/v1/banner/0', null, (data) => {
        data.forEach(item => {
          item.thumbUrl += '-banner'
        })
        this.bannerList = data
      }, null, false)
    },
    loadImage () {
      if (!this.checkloaded) {
        this.checkloaded = true
        this.$refs.scroll.refresh()
      }
    },
    scroll (position) {
      const scrollY = position.y
      if (scrollY < 0) {
        const currentStartIndex = Math.abs(scrollY) <= TopHeight ? 0 : parseInt((Math.abs(scrollY) - TopHeight) / (AreaItemHeight + MarginBottom))
        let currentEndIndex = Math.floor((window.innerHeight - (TopHeight + scrollY) - BottomHeight) / (AreaItemHeight + MarginBottom))
        if (currentEndIndex > this.homeList.length - 1) {
          currentEndIndex = this.homeList.length - 1
        }

        if (currentStartIndex !== this.startIndex) {
          if (currentStartIndex < this.startIndex) {
            this.runAnimation(currentStartIndex)
          }
          this.startIndex = currentStartIndex
        }
        if (currentEndIndex !== this.endIndex) {
          if (currentEndIndex > this.endIndex) {
            this.runAnimation(currentEndIndex)
          }
          this.endIndex = currentEndIndex
        }
      }
    },
    runAnimation (index) {
      animations.registerAnimation({
        name: 'scale',
        animation: [
          {
            scale: 0.5,
            opacity: 0
          },
          {
            scale: 1,
            opacity: 1
          }
        ],
        presets: {
          duration: 300,
          resetWhenDone: true
        }
      })
      animations.runAnimation(this.$refs['area-' + index], 'scale')
    },
    clickBanner (item) {
      // 暂时只处理了跳内容管理系统的
      if (item.type === 0 && item.contentId) {
        this.$router.push(`/content/${item.contentId}/首页Banner`)
      }
    },
    clickItem (id, targetId) {
      switch (id) {
        case '0':
          this.$store.commit('SET_NATIVE_AREA', targetId)
          this.$store.commit('SET_CURRENT_AREA', targetId)
          this.$router.replace(`/address`)
          break
        case '1':
          this.$store.commit('SET_MEITU_TAB', 2)
          this.$router.replace('/meiTu')
          break
        case '2':
          this.$store.commit('SET_MEITU_TAB', 0)
          this.$router.replace('/meiTu')
          break
        case '3':
          this.$router.push(`address/productList/${targetId}/1`)
          break
        case '4':
          this.$router.push(`address/productList/${targetId}/0`)
          break
        case '5':
          this.$vux.toast.text('待开发~')
          break
      }
    },
    toAI () {
      this.$router.push('/chatAI')
    }
  }
}



