
import { localUser } from '@/assets/js/local'
import { isIphoneX } from '@/assets/js/brower'

import axios from '@/api/axiosApi'
import imageTop from '@/assets/img/default_avatar.png'
import imageTopBackround from '@/assets/img/my_top.jpg'
import payImage from '@/assets/img/pay.png'
import waitImage from '@/assets/img/wait.png'
import cancelImage from '@/assets/img/cancel.png'
import failImage from '@/assets/img/fail.png'

import collectionImage from '@/assets/img/my_collection.png'
import browseImage from '@/assets/img/browse.png'
import favoriteImage from '@/assets/img/favorite.png'
import publishImage from '@/assets/img/publish.png'

import messageImage from '@/assets/img/message.png'
import connectUsImage from '@/assets/img/connect_us.png'
import aboutUsImage from '@/assets/img/about_us.png'
import customerServiceImage from '@/assets/img/customer_service.png'

import Scroll from '@/components/scroll/scroll'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters } from 'vuex'

export default {
name: 'User',
data () {
return {
connectUsImage: connectUsImage,
aboutUsImage: aboutUsImage,
customerServiceImage: customerServiceImage,
messageImage: messageImage,
collectionImage: collectionImage,
browseImage: browseImage,
favoriteImage: favoriteImage,
publishImage: publishImage,
payImage: payImage,
waitImage: waitImage,
cancelImage: cancelImage,
failImage: failImage,
headerimage: imageTop,
menusPhone: {
menu1: '拨打官方电话 8888888',
menu2: '拨打投诉电话 1827527847'
},
showPhone: false,
focus: '0',
fans: '0',
user: {},
areaList: [
{
imgBackground: imageTopBackround,
imgHeadPortrait: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1515925352773&di=ec017a532d1078a975466c483b04868d&imgtype=0&src=http%3A%2F%2Fimage.makebizeasy.com%2Fupload%2F8%2Fd7%2F8d7d11a5a1da4efb4ee82cce5d4aded4_thumb.jpg'
}
]
}
},
created () {
this.user = localUser.get() || {}
if (isIphoneX()) {
this.paddingTop = 20
} else {
this.paddingTop = 34
}
if (typeof this.user.id !== 'undefined') {
this.getFocusAndFansCount(this.user.id)
}
},
computed: {
...mapGetters([
'unreadChatCount'
])
},
methods: {
getFocusAndFansCount (userId) {
axios.get(this, '/v1/userInfo/' + userId, null, (data) => {
this.focus = data.followingCount
this.fans = data.followerCount
if (typeof data.headUrl !== 'undefined') {
sessionStorage.setItem('headUrl', data.headUrl)
}
}, null, false)
},
login () {
this.$router.push('/user/login')
},
handleOrderGoto (id) {
this.$router.push('/user/order')
this.$store.commit('SET_ORDER_TAB', id)
},
handleAbout () {
this.$router.push('/user/about')
},
handleCollection () {
this.$router.push('/user/collection')
},
handleBrowse () {
this.$router.push('/user/browse')
},
handleSetting () {
this.$router.push('/user/setting')
},
handlemessage () {
this.$router.push('/user/personmessage')
},
handleArchives () {
this.$router.push('/user/archives' + '/' + this.user.id)
},
handleConnect () {
this.showPhone = true
},
handleClickMenu (value) {
if (value === 'menu1') {
window.location.href = 'tel://8888888'
} else if (value === 'menu2') {
window.location.href = 'tel://182752778747'
}
},
handleMyMessage () {
this.$router.push('/user/messageList')
},
myFocus () {
this.$router.push('/user/focus/' + this.user.id)
},
myFans () {
this.$router.push('/user/fans/' + this.user.id)
},
handleClickToLogin () {
this.$router.push('/user/login')
},
waitDecelop () {
this.$router.push('/chatAI')
},
handleOrderGotoQuitServer (id) {
this.$router.push('/user/orderQuitServer')
this.$store.commit('SET_QUITORDER_TAB', id)
},
handlePublish () {
this.$router.push('/user/publish')
},
handlePraise () {
this.$router.push('/user/praise')
}
},
components: {
Scroll,
Grid,
GridItem,
Cell,
CellBox,
Group,
Badge,
XDialog,
Actionsheet,
XButton
},
watch: {
'$route' (to, from) {
if (to.name === 'User') {
this.user = localUser.get() || {}
if (typeof this.user.id !== 'undefined') {
this.getFocusAndFansCount(this.user.id)
}
}
}
}
}



