const USER = '_user'
const TAG = '_tags_json'

export const localUser = {
  get: (key) => {
    if (typeof key === 'undefined' || key === null) {
      let user = null
      for (let k in localStorage) {
        if (k.indexOf(USER + '_') === 0) {
          if (user === null) {
            user = {}
          }
          user[k.split(USER + '_')[1]] = localStorage.getItem(k)
        }
      }
      return user
    } else {
      return localStorage.getItem(USER + '_' + key)
    }
  },
  set: (user) => {
    if (typeof user !== 'object') {
      console.error('local: user must is Object')
      return
    }
    for (let key in user) {
      localStorage.setItem(USER + '_' + key, user[key])
    }
  },
  setItem: (key, value) => {
    if (typeof key !== 'string') {
      console.error('local: user`s key must is String')
      return
    }
    if (typeof value !== 'string' && typeof value !== 'number') {
      console.error('local: user`s value must is String or Number')
      return
    }
    localStorage.setItem(USER + '_' + key, value)
  },
  remove: (user) => {
    if (typeof user !== 'object') {
      console.error('local: user must is Object')
      return
    }
    for (let key in user) {
      localStorage.removeItem(USER + '_' + key)
    }
  },
  removeItem: (key) => {
    if (typeof key !== 'string') {
      console.error('local: user`s key must is String')
      return
    }
    if (typeof value !== 'string' && typeof value !== 'number') {
      console.error('local: user`s value must is String or Number')
      return
    }
    localStorage.removeItem(USER + '_' + key)
  }
}

export const localTag = {
  get: () => {
    const tmp = localStorage.getItem(TAG)
    if (tmp == null) {
      return null
    }
    return JSON.parse(tmp)
  },
  set: (tag) => {
    if (!(tag instanceof Array)) {
      console.error('local: tags must is Array')
      return
    }
    localStorage.setItem(TAG, JSON.stringify(tag))
  },
  push: (tag) => {
    let tmp = localStorage.getItem(TAG)
    if (tmp == null) {
      tmp = []
    } else {
      tmp = JSON.parse(tmp)
    }
    if (tag instanceof Array) {
      for (var i = 0; i < tag.length; i++) {
        const findIndex = tmp.findIndex(item => item.id === tag[i].id)
        if (findIndex < 0) {
          tmp.push(tag[i])
        }
      }
    } else {
      const findIndex = tmp.findIndex(item => item.id === tag.id)
      if (findIndex < 0) {
        tmp.push(tag)
      }
    }
    localStorage.setItem(TAG, JSON.stringify(tmp))
  },
  remove: (tagId) => {
    let tmp = localStorage.getItem(TAG)
    if (tmp == null) {
      console.error('local: tags is null')
      return
    }
    tmp = JSON.parse(tmp)
    const findIndex = tmp.findIndex(item => item.id === tagId)
    if (findIndex < 0) {
      console.error('local: tags中没有包含[' + tagId + ']的标签')
      return
    }
    tmp.splice(findIndex, 1)
    localStorage.setItem(TAG, JSON.stringify(tmp))
  },
  removeAll: () => {
    localStorage.removeItem(TAG)
  }
}



