import Vue from 'vue'
import Router from 'vue-router'

// 五个Tab页全部预加载
import Home from '@/pages/home/home'
import Address from '@/pages/address/address'
import MeiTu from '@/pages/meiTu/meiTu'
import Cart from '@/pages/cart/cart'
import User from '@/pages/User/User'
Vue.use(Router)

// 其他的页面懒加载

// const ProductList = () => import('@/pages/address/product/productList')
const Product = () => import('@/pages/cart/cart-page/scenic-spot/product')
const ProductOrder = () => import('@/pages/cart/cart-page/scenic-spot/productOrder')
const AddressMap = () => import('@/pages/address/map/map')
const Invoice = () => import('@/pages/cart/cart-page/scenic-spot/invoice')
const ProductCommentDetails = () => import('@/pages/cart/cart-page/scenic-spot/productCommentDetails')
const DetailServiceMap = () => import('@/pages/cart/cart-page/scenic-spot/detailServiceMap')
const SpecialLocalProductDetail = () => import('@/pages/cart/cart-page/specialLocal-product/specialLocal-product-detail')
// const Food = () => import('@/pages/address/food/food')
const FoodDescribe = () => import('@/pages/address/food/foodDescribe')
const StoreDescribe = () => import('@/pages/address/food/storeDescribe')
const Amusement = () => import('@/pages/address/amusement/amusement')
const Strategy = () => import('@/pages/address/strategy/strategy')
const StrategyList = () => import('@/pages/address/strategy/strategyList')

const UserDynamic = () => import('@/pages/meiTu/userDynamic/userDynamic')
const TagDynamic = () => import('@/pages/meiTu/tagDynamic/tagDynamic')
const PublishDynamic = () => import('@/pages/meiTu/publishDynamic/publishDynamic')
const SelectTag = () => import('@/pages/meiTu/publishDynamic/selectTag')
const GetAddress = () => import('@/pages/meiTu/publishDynamic/getAddress')
const SendComment = () => import('@/pages/meiTu/sendComment/sendComment')
const DynamicDesc = () => import('@/pages/meiTu/dynamicDesc/dynamicDesc')

const Login = () => import('@/pages/user/login/login')
const Register = () => import('@/pages/user/login/register')
const ForgetPass = () => import('@/pages/user/login/forgetPass')
const BindPhone = () => import('@/pages/user/login/bindPhone')
const Order = () => import('@/pages/user/order/order')
const OrderDesc = () => import('@/pages/user/order/orderdesc')
const OrderQuitServer = () => import('@/pages/user/order/orderQuitServer')
const About = () => import('@/pages/user/about/about')
const Collection = () => import('@/pages/user/collection/collection')
const Browse = () => import('@/pages/user/browse/browse')
const Setting = () => import('@/pages/user/setting/setting')
const PersonMessage = () => import('@/pages/user/editmessage/personmessage')
const Fans = () => import('@/pages/user/follower/fans')
const Focus = () => import('@/pages/user/follower/focus')
const Evalution = () => import('@/pages/user/order/evalution')
const MessageList = () => import('@/pages/user/message/messageList')
const Publish = () => import('@/pages/user/publish/publish')
const Praise = () => import('@/pages/user/publish/praise')
const Archives = () => import('@/pages/user/archives/archives')
const EvalutionDesc = () => import('@/pages/user/archives/evalutiondesc')
const QuestionAndAnswer = () => import('@/pages/user/archives/questionandanswer')
const AnswerDesc = () => import('@/pages/user/archives/answerdesc')
const PublishAnswer = () => import('@/pages/user/archives/publishanswer')
const Report = () => import('@/pages/user/archives/report')

const Chat = () => import('@/components/chat/chat')
const ChatAI = () => import('@/components/chat/chatAI')

const Content = () => import('@/pages/commonContent/content')

const RouterInstance = new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      children: [
      ]
    },
    {
      path: '/address',
      name: 'Address',
      component: Address,
      children: [
        {
          path: 'map/:addressId',
          name: 'AddressMap',
          component: AddressMap
        },
        {
          path: 'foodDescribe/:id/:contentId',
          name: 'FoodDescribe',
          component: FoodDescribe
        },
        {
          path: 'storeDescribe/:contentId',
          name: 'StoreDescribe',
          component: StoreDescribe
        },
        {
          path: 'amusement/:addressId/:categoryId',
          name: 'Amusement',
          component: Amusement
        },
        {
          path: 'strategy',
          name: 'Strategy',
          component: Strategy
        },
        {
          path: 'strategyList/:strategyId',
          name: 'StrategyList',
          component: StrategyList
        }
      ]
    },
    {
      path: '/meiTu',
      name: 'MeiTu',
      component: MeiTu,
      children: [
        {
          path: 'publishDynamic',
          name: 'PublishDynamic',
          component: PublishDynamic,
          children: [
            {
              path: 'selectTag',
              name: 'SelectTag',
              component: SelectTag
            },
            {
              path: 'getAddress',
              name: 'GetAddress',
              component: GetAddress
            }
          ]
        },
        {
          path: 'sendComment/:dynamicId',
          name: 'SendComment',
          component: SendComment
        },
        {
          path: 'dynamicDesc/:dynamicId',
          name: 'DynamicDesc',
          component: DynamicDesc
        },
        {
          path: 'userDynamic/:userId',
          name: 'UserDynamic',
          component: UserDynamic
        },
        {
          path: 'tagDynamic/:tagId',
          name: 'TagDynamic',
          component: TagDynamic
        }
      ]
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart,
      children: [
        {
          path: 'product/:productId/:type',
          name: 'Product',
          component: Product
        },
        {
          path: 'productOrder/:packageId',
          name: 'ProductOrder',
          component: ProductOrder
        },
        {
          path: 'productCommentDetails',
          name: 'ProductCommentDetails',
          component: ProductCommentDetails
        },
        {
          path: 'Invoice',
          name: 'Invoice',
          component: Invoice
        },
        {
          path: 'detailServiceMap/:productId',
          name: 'detailServiceMap',
          component: DetailServiceMap
        }
      ]
    },
    {
      path: '/user',
      name: 'User',
      component: User,
      children: [
        {
          path: 'login',
          name: 'Login',
          component: Login,
          children: [
            {
              path: 'register',
              name: 'Register',
              component: Register
            },
            {
              path: 'bindPhone',
              name: 'BindPhone',
              component: BindPhone
            },
            {
              path: 'forgetPass',
              name: 'ForgetPass',
              component: ForgetPass
            }
          ]
        },
        {
          path: 'order',
          name: 'Order',
          component: Order,
          children: [
            {
              path: 'orderDesc/:descId',
              name: 'OrderDesc',
              component: OrderDesc
            }
          ]
        },
        {
          path: 'evalution/:productId',
          name: 'Evalution',
          component: Evalution
        },
        {
          path: 'orderQuitServer',
          name: 'OrderQuitServer',
          component: OrderQuitServer
        },
        {
          path: 'about',
          name: 'About',
          component: About
        },
        {
          path: 'collection',
          name: 'Collection',
          component: Collection
        },
        {
          path: 'browse',
          name: 'Browse',
          component: Browse
        },
        {
          path: 'setting',
          name: 'Setting',
          component: Setting
        },
        {
          path: 'personmessage',
          name: 'PersonMessage',
          component: PersonMessage
        },
        {
          path: 'fans/:userId',
          name: 'Fans',
          component: Fans
        },
        {
          path: 'focus/:userId',
          name: 'Focus',
          component: Focus
        },
        {
          path: 'messageList',
          name: 'MessageList',
          component: MessageList
        },
        {
          path: 'publish',
          name: 'Publish',
          component: Publish
        },
        {
          path: 'praise',
          name: 'Praise',
          component: Praise
        },
        {
          path: 'archives/:userId',
          name: 'Archives',
          component: Archives
        },
        {
          path: 'evalutionDesc',
          name: 'EvalutionDesc',
          component: EvalutionDesc
        },
        {
          path: 'questionandanswer',
          name: 'QuestionAndAnswer',
          component: QuestionAndAnswer
        },
        {
          path: 'answerdesc',
          name: 'AnswerDesc',
          component: AnswerDesc
        },
        {
          path: 'publishanswer',
          name: 'PublishAnswer',
          component: PublishAnswer
        },
        {
          path: 'report',
          name: 'Report',
          component: Report
        }
      ]
    },
    {
      path: '/chat',
      name: 'Chat',
      component: Chat
    },
    {
      path: '/chatAI',
      name: 'ChatAI',
      component: ChatAI
    },
    {
      path: '/content/:contentId/:title',
      name: 'Content',
      component: Content
    },
    {
      path: '/specialLocalProductDetail',
      name: 'SpecialLocalProductDetail',
      component: SpecialLocalProductDetail
    }
  ]
})

export default RouterInstance



