
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { isIphoneX } from '@/assets/js/brower'
import { mapGetters } from 'vuex'

export default {
components: {
Tabbar, TabbarItem
},
created () {
this.paddingBottom = 0
if (isIphoneX()) {
this.paddingBottom = 34
}

if (this.$route.name === 'Home' || this.$route.name === 'Address' || this.$route.name === 'MeiTu' || this.$route.name === 'Cart' || this.$route.name === 'User') {
this.isShow = true
this.tabList.forEach(tab => {
if (tab.name.toUpperCase() === this.$route.name.toUpperCase()) {
this.currentTab = tab.name
tab.icon = tab.icon.replace('off', 'on')
} else {
tab.icon = tab.icon.replace('on', 'off')
}
})
} else {
this.isShow = false
}
},
data () {
return {
isShow: true,
currentTab: 'home',
tabList: [
{
name: 'home',
title: '首页',
icon: './static/img/tab/home_on.png',
showDot: false
},
{
name: 'address',
title: '目的地',
icon: './static/img/tab/address_off.png',
showDot: false
},
{
name: 'meiTu',
title: '美途',
icon: './static/img/tab/meiTu_off.png',
showDot: true
},
{
name: 'cart',
title: '商城',
icon: './static/img/tab/cart_off.png',
showDot: false
},
{
name: 'user',
title: '我的',
icon: './static/img/tab/user_off.png',
showDot: false,
badge: ''
}
]
}
},
computed: {
...mapGetters([
'unreadChatCount'
])
},
methods: {
toTab (name) {
this.tabList.forEach(tab => {
if (tab.name === name) {
tab.icon = tab.icon.replace('off', 'on')
} else {
tab.icon = tab.icon.replace('on', 'off')
}
})
this.$router.replace(name)
}
},
watch: {
'$route' (to, from) {
if (to.name === 'Home' || to.name === 'Address' || to.name === 'MeiTu' || to.name === 'Cart' || to.name === 'User') {
this.isShow = true
this.tabList.forEach(tab => {
if (tab.name.toUpperCase() === to.name.toUpperCase()) {
this.currentTab = tab.name
tab.icon = tab.icon.replace('off', 'on')
} else {
tab.icon = tab.icon.replace('on', 'off')
}
})
} else {
this.isShow = false
}
},
unreadChatCount (newVal, oldVal) {
if (newVal > 99) {
this.tabList[4].badge = '99'
} else if (newVal <= 0) {
this.tabList[4].badge = ''
} else {
this.tabList[4].badge = newVal + ''
}
}
}
}



