
import axios from '@/api/axiosApi'
import defaultAvatar from '@/assets/img/default_avatar.png'
import { localUser } from '@/assets/js/local'
import { isIphoneX } from '@/assets/js/brower'
import { QINIU_PREFIX } from '@/api/config'
import { mapGetters, mapMutations } from 'vuex'

import Scroll from '@/components/scroll/scroll'
import Waterfall from '@/components/waterfall/waterfall'
import IndexList from '@/components/index-list/index-list'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
data() {
return {
showSelectAddress: false,
topicList: [],
recommendList: []
}
},
directives: {
TransferDom
},
components: {
Scroll, Waterfall, Popup, IndexList
},
computed: {
itemWidth () {
return (window.innerWidth - 21) / 2
},
...mapGetters([
'currentArea', 'areaList'
])
},
created() {
this.paddingBottom = 0
if (isIphoneX()) {
this.paddingBottom = 34
}
this.pageNum = 1
this._getHotTag()
this._getRecommendDynamic()
},
methods: {
_getHotTag() {
const _self = this
axios.get(this, '/v1/tag', {
hotTag: 1,
pageSize: 5
}, (data) => {
_self.topicList = data
_self.$nextTick(() => {
_self.$refs.topicList.style.width = ((120 + 6) * _self.topicList.length) + 'px'
})
})
},
_getRecommendDynamic() {

const userId = localUser.get('id')
const _self = this
axios.get(_self, '/v1/dynamic', {
pageSize: 15,
pageNum: _self.pageNum,

curUserId: userId
}, (data) => {
if (data.length === 0) {
if (_self.pageNum > 0) {
if (_self.$refs.scrollWrapper) {
_self.pageNum--
_self.$refs.scrollWrapper.hiddenPullUpLoad()
}
} else {
_self.$refs.scrollWrapper.hiddenPullDown()
}
return
}
data.forEach(item => {
let tmpHeadUrl = defaultAvatar
if (item.headUrl) {
if (item.headUrl.indexOf(QINIU_PREFIX) > -1) {
tmpHeadUrl = item.headUrl + '-bheader'
} else {
tmpHeadUrl = item.headUrl
}
}
item.headUrl = tmpHeadUrl
item.src = item.atlas ? item.atlas.split(';')[0] + '-w350' : ''
item.publishTime = _self.moment(item.publishTime).from(_self.moment())
})

if (_self.pageNum <= 1) {
_self.recommendList = data
} else {
_self.recommendList = _self.recommendList.concat(data)
}
}, null, false)
},
onPullingDown() {
this.pageNum = 1
this._getRecommendDynamic()
},
onPullingUp() {
this.pageNum++
this._getRecommendDynamic()
},
updateMessage (dynamicId, value) {
const userId = localUser.get('id')
if (userId == null) {
this.$vux.toast.text('请先登录！', 'middle')
return
}
if (this.sendRequest) {
return
}
let params = {
userId: userId,
targetId: dynamicId,
type: 1,
starType: value ? 0 : 1
}
const _self = this
this.sendRequest = true
axios.post(this, '/v1/messageRecord', params, (data) => {
_self.sendRequest = false
const index = _self.recommendList.findIndex(o => o.id === dynamicId)
if (value) {
_self.recommendList[index].star = 0
_self.recommendList[index].starSum--
} else {
_self.recommendList[index].star = 1
_self.recommendList[index].starSum++
}
}, () => {
_self.sendRequest = false
}, false)
},
toTag(tagId) {
this.$router.push(`/meiTu/tagDynamic/${tagId}`)
},
toDynamicDesc (item) {
this.$router.push(`/meiTu/dynamicDesc/${item.id}`)
},
selectArea (area) {
this.setCurrentArea(area)
this.showSelectAddress = false
this.area = area
},
...mapMutations({
setCurrentArea: 'SET_CURRENT_AREA'
})
}
}



