import headerBg from '@/assets/img/header-bg.png'
import { isIphoneX } from '@/assets/js/brower'

export default {
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    searchPlaceHolder: {
      type: String,
      default: '搜索...'
    }
  },
  data () {
    return {
      headerBg,
      paddingTop: 0,
      searchValue: '',
      showSearchBtn: false
    }
  },
  created () {
    if (isIphoneX()) {
      this.paddingTop = 34
    } else {
      this.paddingTop = 20
    }
  },
  mounted () {
    this.$refs.search.style.width = window.innerWidth - this.$refs.button.clientWidth - 30 - 24 - 60 - 18 + 'px'
  },
  methods: {
    back () {
      window.history.length === 0 ? this.$router.replace('/') : this.$router.go(-1)
    },
    change () {
      this.$emit('change', this.searchValue)
    },
    removeSearch () {
      this.searchValue = ''
      this.$emit('removeSearch')
    },
    submitSearch () {
      this.$emit('submitSearch', this.searchValue)
    }
  },
  watch: {
    searchValue (newValue, oldValue) {
      if (newValue.length > 0) {
        this.showSearchBtn = true
      } else {
        this.showSearchBtn = false
      }
    },
    value (val) {
      this.searchValue = val
    }
  }
}



