
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'


import iconWechat from '@/assets/img/icon/share_wechat.png'
import iconFriend from '@/assets/img/icon/share_friend.png'
import iconQQ from '@/assets/img/icon/share_qq.png'
import nativeBridge from '@/assets/js/nativeBridge'

export default {
model: {
prop: 'value',
event: 'toggle'
},
props: {
value: Boolean,
title: String,
desc: String,
thumbUrl: String,
linkUrl: String
},
data () {
return {
showShare: this.value,
iconWechat,
iconFriend,
iconQQ
}
},
directives: {
TransferDom
},
components: {
Popup
},
methods: {
shareToWechat (type) {
this.showShare = false
nativeBridge.shareToWechat(this, type, this.title, this.desc, this.thumbUrl, this.linkUrl)
},
toggle (val) {
this.$emit('toggle', val)
}
},
watch: {
value (val) {
this.showShare = val
}
}
}



