
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import Slider from '@/components/slider/slider'
import axios from '@/api/axiosApi'
import Divider from '@/components/divider/divider'
import SearchHeader from '@/components/seach-header/seach-header'
import Rater from 'vux/src/components/rater/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'



import bannerList from '@/assets/json/bannerAddress.json'

export default {
data () {
return {
bannerList,
headerOpacity: 0,
tagWidth: 1200,
currentPosition: [],
currentSort: 0,
addressInfo: {},
sortTools: [
{
label: '智能排序',
value: 0
},
{
label: '价格↑',
value: 1
},
{
label: '价格↓',
value: 2
},
{
label: '距离↑',
value: 3
}
],
foodCategoryList: [],
storeFoodList: [],
productList: [],
searchList: [],
tagNameList: [],
foodCategoryRecommendList: [],
box1Width: 0,
box1ItemWidth: '',

hotTags: [{
id: '',
name: '全部'
}],
currentTag: '',

showSearch: false,
seachValue: ''
}
},
directives: {
TransferDom
},
components: {
VHeader, Scroll, Rater, Tab, TabItem, Popup, Slider, Grid, GridItem, Scroller, Divider, Flexbox, FlexboxItem, SearchHeader
},
mounted () {
this.$refs.scroll.$el.style.height = (window.innerHeight - this.$refs.scroll.$el.offsetTop) + 'px'
this.box1Width = this.box1ItemWidth * this.foodCategoryRecommendList.length + 15
this.$refs.tagWrapper.style.width = this.imgList.length * (140 + 10) + 'px'
},
created () {
this.getFoodCategoryRecommend(1)
this.getFoodCategory()
this._getBanner()
this._getHotTags()
},
methods: {
back () {
this.$router.go(-1)
},
scroll (position) {
if (position.y <= -100) {
this.headerOpacity = 1
} else {
this.headerOpacity = Math.abs(position.y / 168)
}
},
getFoodCategoryRecommend (recommend) {
axios.get(this, `/v1/foodCategory`, {recommend: 1}, (data) => {
data.forEach(item => {
this.tagNameList = item.tagNames.split(';')
})
this.foodCategoryRecommendList = data

})
},

getFoodCategory () {
axios.get(this, `/v1/foodCategory`, {tagIds: this.currentTag}, (data) => {
data.forEach(item => {
this.tagNameList = item.tagNames.split(';')
})
this.foodCategoryList = data

})
},
handleGoFoodDescribe (id, contentId) {
this.$router.push('/address/foodDescribe/' + id + '/' + contentId)
},
handleBoxItem (id, contentId) {
this.$router.push('/address/foodDescribe/' + id + '/' + contentId)
},

_getBanner () {
axios.get(this, '/v1/banner/3', null, (data) => {
data.forEach(item => {
item.thumbUrl += '-banner'
})
this.bannerList = data
}, null, false)
},
clickBanner (type, contentId, name) {

if (type === 0 && contentId) {
this.$router.push(`/content/${contentId}/${name}`)
}
},

_getHotTags () {
axios.get(this, `/v1/tag`, {
addressId: this.$route.params.addressId,
type: 4,
pageSize: 12
}, (data) => {
data.splice(0, 0, {
id: '',
name: '全部'
})
this.hotTags = data
if (data.length > 0) {
this.currentTag = data[0].id
}
setTimeout(() => {
let w = 0
const eles = this.$refs.tagWrapper.children
for (let i = 0; i < eles.length; i++) {
const item = eles[i]
w += item.offsetWidth + 12
}
this.$refs.tagWrapper.style.width = w + 'px'
}, 50)
})
},
clickTag (tagId) {
this.currentTag = tagId
this.getFoodCategory()
},

submitSearch (value) {
axios.get(this, `/v1/foodCategory`, {
name: value,
pageSize: 10
}, (data) => {
this.searchList = data
})
},
clickSearchItem (item) {
this.clickItem(item.contentId, item.name, item.id)
this.showSearch = false
this.seachValue = ''
this.searchList = []
},
clickItem(contentId, name, id) {
if (contentId) {
this.$router.push(`/address/foodDescribe/` + id + '/' + contentId)
}
}
},
watch: {
}
}



