
import headerBg from '@/assets/img/header-bg.png'
import { isIphoneX } from '@/assets/js/brower'
import { localUser } from '@/assets/js/local'
import uploadFile from '@/api/uploadFile'
import defaultAvatar from '@/assets/img/default_avatar.png'

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import { mapGetters, mapMutations } from 'vuex'
import Traveling from './meiTuTab/traveling'
import Native from './meiTuTab/native'
import Follow from './meiTuTab/follow'

export default {
name: 'MeiTu',
data () {
return {
headerBg,
defaultAvatar,
headerList: [
{
value: 0,
label: '正在旅行'
},
{
value: 1,
label: '本地人'
},
{
value: 2,
label: '关注'
}
],
selectAtlas: [],
showPlus: true,
showSelectType: false
}
},
computed: {
...mapGetters({
current: 'meiTuTab'
})
},
created () {
this.tabHeight = 50
this.tabPadding = 20
if (isIphoneX()) {
this.tabPadding = 34
}
this.isLocal = localUser.get('local')
this.swiperHeight = window.innerHeight - 45 - 50 - this.tabPadding
},
directives: {
TransferDom
},
components: {
Tab, TabItem, Native, Follow, Traveling, Popup
},
methods: {
publishDynamic () {
const user = localUser.get()
if (user == null) {
this.$vux.toast.text('请先登录！', 'middle')
this.$router.push('/user/login')
return
}
this.$refs.selectImg.dispatchEvent(new MouseEvent('click'))
},
selectImg (e) {
if (e.target.files.length > 0 && e.target.files[0].size === 0) return
if (e.target.files.length > 15) {
this.$vux.toast.text('最多只能选15张图片', 'middle')
return
}
const _self = this
uploadFile(this, e.target.files, (data) => {
let temp = []
data.forEach(item => {
temp.push(item.downloadPath)
})
e.target.value = ''
_self.setSelectPic(temp)
}, null, false)
_self.$router.push('/meiTu/publishDynamic')
},
sendDynamic (type) {
const user = localUser.get()
if (user == null) {
this.$router.push('/user/login')
return
}
this.showSelectType = false
switch (type) {
case 0:
this.$router.push('/meiTu/publishDynamic')
break
case 1:
this.$router.push('/meiTu/publishDynamic')
break
case 2:
this.$router.push('/meiTu/publishDynamic')
break
case 3:
this.$router.push('/meiTu/publishDynamic')
break
default:
}
},
...mapMutations({
setMeiTuTab: 'SET_MEITU_TAB',
setSelectPic: 'SET_SELECT_PIC'
})
},
watch: {
'$route' (to, from) {
if (to.name === 'MeiTu') {
this.showPlus = true
} else {
this.showPlus = false
}
}
}
}



