
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'


import axios from '@/api/axiosApi'
import Divider from '@/components/divider/divider'
import headerBg from '@/assets/img/header-bg.png'
import cartComment from '@/pages/cart/cart-page/cart-comment/cart-comment'
import SpecialLocalProduct from '@/pages/cart/cart-page/specialLocal-product/specialLocal-product'
import Guide from '@/pages/cart/cart-page/guide/guide'
import CarRental from '@/pages/cart/cart-page/car-rental/car-rental'
import CountryTour from '@/pages/cart/cart-page/country-tour/country-tour'
import ProductList from '@/pages/cart/cart-page/scenic-spot/productList'
import Food from '@/pages/cart/cart-page/food/food'
import SearchHeader from '@/components/seach-header/seach-header'
import hotelQuery from '@/pages/cart/cart-page/hotel/hotel-query'



const cartNav1 = () => ['推荐', '景区', '酒店', '特产', '美食']
export default {
name: 'Cart',
data () {
return {
tabIndex: 0,
featureList: [{}],
cartNav: cartNav1(),
headerBg,
showSearch: false,
seachValue: ''
}
},
components: {

Scroller,
Tab,
TabItem,
Divider,
Swiper,
SwiperItem,
cartComment,
SpecialLocalProduct,
Guide,
CarRental,
CountryTour,
ProductList,
Food,
Popup,
SearchHeader,
hotelQuery
},



mounted () {

this._getFeature()
},
methods: {
_getFeature (type) {
const _self = this
axios.get(_self, '/v1/featureProduct', {
recommend: type || ''
}, (data) => {
let recommendCount = 0
data.forEach(item => {
if (item.recommend === 1) {
recommendCount++
}
})
_self.$refs.scrollRecom.style.width = (200 + 10) * recommendCount + 'px'
_self.featureList = data
})
},
toContent (goods) {
if (goods.contentId) {
this.$router.push(`/content/${goods.contentId}/${goods.name}`)
} else {
this.$vux.toast.text('该商品无详情数据', 'middle')
}
},
scroll (position, movingDirectionY) {
},
toMap () {
alert('这里是搜索！')
},
onItemClick (index) {




},
submitSearch (value) {
axios.get(this, `/v1/product`, {
name: value,
type: this.area.type,
pageSize: 10
}, (data) => {
this.searchList = data
})
}
}
}



