import Tab from '@/components/tab/tab'

import axios from '@/api/axiosApi'
import { isIphoneX } from '@/assets/js/brower'
import { on } from '@/assets/js/dom'
import nativeBridge from '@/assets/js/nativeBridge'

export default {
  name: 'app',
  components: {
    Tab
  },
  data () {
    return {
      // 也可以用正则表达式
      keepAliveComponents: ['Home', 'Address', 'MeiTu', 'Cart', 'User', 'ChatAI'],
      unKeepAliveCommponents: []
    }
  },
  created () {
    this.appHeight = document.body.offsetHeight
    if (isIphoneX()) {
      this.routerHeight = this.appHeight - 51 - 34
    } else {
      this.routerHeight = this.appHeight - 51
    }
    this.$store.dispatch('initJIM')

    AMap.plugin(['AMap.Scale', 'AMap.ToolBar'])

    on(window, 'touchmove', this.touchListener)
    on(window, 'touchstart', this.touchListener)

    this._getAddressList()

    const _self = this
    nativeBridge.getPosition(this, (arg) => {
      _self.$store.commit('SET_POSITION', [parseFloat(arg.longitude), parseFloat(arg.latitude)])
    })
  },
  methods: {
    _getAddressList () {
      axios.get(this, `/v1/address/1`, {
        pageSize: 50
      }, (data) => {
        let temp = []
        data.forEach(item => {
          const findIndex = temp.findIndex((o) => o.name === item.nameSpell.toUpperCase()[0])
          if (findIndex < 0) {
            temp.push({
              name: item.nameSpell.toUpperCase()[0],
              items: [item]
            })
          } else {
            temp[findIndex].items.push(item)
          }
        })
        temp.sort((o1, o2) => o1.name.charCodeAt(0) - o2.name.charCodeAt(0))
        this.$store.commit('SET_AREA_LIST', temp)
      })
    },
    touchListener (e) {
      // 去除IOS 浏览器本身的回弹效果
      if (!e._isScroller) {
        // 判断默认行为是否可以被禁用
        if (e.cancelable) {
          // 判断默认行为是否已经被禁用
          if (!e.defaultPrevented) {
            e.preventDefault()
          }
        }
      }
      // 去除网络加载时和better-scroll的滚动冲突
      if (document.getElementsByClassName('vux-loading').length > 0 && document.getElementsByClassName('vux-loading')[0].style.display !== 'none') {
        if (e.cancelable) {
          if (!e.defaultPrevented) {
            e.preventDefault()
          }
        }
      }
    }
  }
}



