avgPrice: '12',
address: '这里是地址',
rate: '4'
},
{
img: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1522164981548&di=c3e5cbbea0d74f6220655a2c00b71a89&imgtype=0&src=http%3A%2F%2Fi0.hdslb.com%2Fbfs%2Farchive%2F9ffa6a877a5b3385144d395d1471bd2325c8ff80.jpg',
name: '景区',
feature: '这里是景区',
