...mapGetters([
'currentArea'
])
},
components: {
Slider,
scroll,
Scroller,
Tab,
TabItem,
Rater,
Divider
},
mounted () {
this.$refs.cartInnerHeight.$el.style.height = (window.innerHeight - 110) + 'px'
this.$refs.scrollRecom.style.width = (200 + 10) * this.imgList.length + 'px'
},
methods: {
onItemClick () {
this.cartType = 0
},
onItemClickRe () {
this.cartType = 1
},
toMap () {
this.$router.push(`/address/map/${this.currentArea.id}`)
}
}
}



