import headerBg from '@/assets/img/header-bg.png'
import { isIphoneX } from '@/assets/js/brower'

export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    fontColor: {
      type: String,
      default: '#fff'
    },
    opacity: {
      type: Number,
      default: 1
    },
    showBackground: {
      type: Boolean,
      default: true
    },
    customBackground: {
      type: String,
      default: null
    },
    fixed: {
      type: null,
      default: false
    },
    handleClick: {
      type: null,
      default: false
    }
  },
  data () {
    return {
      headerBg,
      paddingTop: 0
    }
  },
  created () {
    if (isIphoneX()) {
      this.paddingTop = 34
    } else {
      this.paddingTop = 20
    }
  },
  methods: {
    back () {
      this.$emit('back')
      if (!this.handleClick) {
        window.history.length === 0 ? this.$router.replace('/') : this.$router.go(-1)
      }
    }
  }
}



