
import dynamicItem from '@/pages/meiTu/dynamicList/dynamicItem'
import Scroll from '@/components/scroll/scroll'
import Previewer from '@/components/previewer/previewer'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import Share from '@/components/share/share'

import { isIphoneX } from '@/assets/js/brower'

export default {
props: {
isMe: {
type: Boolean,
default: false
},
list: {
type: Array,
default: []
},
pullUpLoad: {
type: Boolean,
default: true
},
pullDownRefresh: {
type: Boolean,
default: true
}
},
data () {
return {
options: {
loop: false
},
showShare: false,
shareContent: {},
selectAtlas: []
}
},
created() {
this.bottom = 0
if (isIphoneX()) {
this.bottom = 34
}
},
directives: {
TransferDom
},
components: {
Previewer, dynamicItem, Scroll, Popup, Share
},
methods: {
scrollStart () {
const ele = document.getElementsByClassName('bottom-tab')
if (ele[0]) {
ele[0].style.transform = 'translateZ(105px)'
}
},
scrollEnd (position) {
const ele = document.getElementsByClassName('bottom-tab')
if (ele[0]) {
ele[0].style.transform = 'translateZ(0px)'
}
},
scroll (position, directionY) {
this.$emit('scroll', position, directionY)
},
onPullingUp () {
this.$emit('onPullingUp')
},
onPullingDown () {
this.$emit('onPullingDown')
},
refresh () {
this.$refs.scrol.forceUpdate(true)
},
clickImg (imgs, index) {

this.$vux.loading.show({
text: '加载中...',
position: 'absolute'
})
let temp = []
imgs.forEach((img, i) => {
temp.push({
src: img
})
})
this.selectAtlas = temp
this.$nextTick(() => {
this.$refs.previewer.show(index)
})
},
toShare (obj) {
this.showShare = true
this.shareContent = obj
},
toggleShare (value) {
this.showShare = value
},
preImgLoaded () {
this.$vux.loading.hide()
}
}
}



