
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import Divider from '@/components/divider/divider'
import IndexList from '@/components/index-list/index-list'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import SearchHeader from '@/components/seach-header/seach-header'
import Waterfall from '@/components/waterfall/waterfall'
import TWEEN from '@tweenjs/tween.js'

import icon from '@/assets/json/addressIcon.json'
import defaultAvatar from '@/assets/img/default_avatar.png'

import getWeather from '@/api/getWeather'
import { isIphoneX } from '@/assets/js/brower.js'
import axios from '@/api/axiosApi'
import { mapGetters, mapMutations } from 'vuex'
import { QINIU_PREFIX } from '@/api/config'
import { localUser } from '@/assets/js/local'

export default {
name: 'Address',
data () {
return {
icon,
clickBack: false,
showLasted: false,
showSearch: false,
showAtlas: false,
showSelectAddress: false,
opacity: 0,
featureWidth: 500,
activeMarqueeIndex: 0,
marqueeAnimate: false,
area: {
id: 'ba062c32fdf611e7ba2d00163e0c27f8',
name: '凯里',
thumbUrl: '',
longitude: '',
latitude: ''
},
seachValue: '',
userList: [],   // 去过的人
productList: [],  // 特色主题
weather: {
weather: '多云',
temperature: 12
},
recommendRate: 30,
recommendRateCopy: 30,
user: null,
amusementList: [],  // 玩乐列表
pushList: [],   // 滚动列表
hostDynamic: [],  // 热门动态
searchList: [],   // 搜索列表
localImgs: [],    // 当地的6张图片
atlas: [],        // 图集
messageRecord: [] 
}
},
computed: {
itemWidth () {
return (window.innerWidth - 21) / 2
},
marqueeTop () {
return -this.activeMarqueeIndex * 51 + 'px'
},
...mapGetters([
'currentArea', 'areaList'
])
},
directives: {
TransferDom
},
components: {
VHeader, Scroll, Marquee, MarqueeItem, Divider, Scroller, Waterfall, Popup, SearchHeader, IndexList, Grid, GridItem
},
created () {
this.paddingBottom = 0
if (isIphoneX()) {
this.paddingTop = 40 + 34
this.atlasScrollHeight = window.innerHeight - (40 + 34)
this.paddingBottom = 20
} else {
this.paddingTop = 40 + 20
this.atlasScrollHeight = window.innerHeight - (40 + 20)
}

this.dynamicWidth = (window.innerWidth - 30) / 2 - 10
this.showSearch = false


getWeather(this.area.name, data => {
this.weather = data
})
},
activated () {

this._getAreaInfo(this.area.id)
this.user = localUser.get()

this.startMarquee(3500)
},
deactivated() {
this.stopMarquee()
},
destroyed () {
this.stopMarquee()
},
methods: {
_getAreaInfo (id) {
const _self = this
axios.get(this, `/v1/addressInfo/${id}`, null, (data) => {
data.isRefresh = true
_self.area = data
_self.setCurrentArea(data)
if (data.localAmusement) {
const t1 = data.localAmusement.split(';')
_self.amusementList = []
t1.forEach(tt => {
_self.amusementList.push({
id: tt.split(',')[0],
name: tt.split(',')[1],
img: tt.split(',')[2]
})
})
} else {
_self.amusementList = []
}
getWeather(_self.area.name, params => {
this.weather = params
})
_self._getPlaceOrder()
_self._getProductList()
_self._getHostDynamic()
_self._getRecommendRate()
_self._getUserList()
_self._getAtlas(true)
}, null, false)
},
_getAtlasNum () {
const _self = this
axios.get(this, `/v1/atlas/${this.area.id}/no`, null, (data) => {
_self.area.atlasNum = data
}, null, false)
},
_getUserList () {
const _self = this
axios.get(this, `/v1/users/${this.area.id}`, {
pageSize: 3
}, (data) => {
_self.userList = data
}, null, false)
},
_getPlaceOrder () {
const _self = this
axios.get(this, `/v1/placeOrder`, {
pageSize: 10
}, (data) => {
data.forEach((item) => {
if (item.headUrl == null || item.headUrl === '') {
item.headUrl = defaultAvatar
}
})
_self.pushList = data
}, null, false)
},
_getProductList () {
axios.get(this, '/v1/product', {
recommend: 1,
areaId: this.area.id
}, (data) => {
data.forEach(item => {

})
this.featureWidth = data.length * (145 + 13)
this.productList = data
})
},
_getHostDynamic () {
const _self = this
axios.get(this, `/v1/dynamic/${this.area.id}`, null, (data) => {
data.forEach(item => {
item.src = item.atlas ? item.atlas.split(';')[0] + '-w350' : ''
item.publishTime = this.moment(item.publishTime).format('YYYY年MMMDo')
})
_self.hostDynamic = data
_self.$nextTick(() => {
_self.startMarquee(3500)
})
})
},
_getRecommendRate () {
axios.get(this, `/v1/travelScore/${this.area.id}`, null, (data) => {
this.recommendRate = data
}, null, false)
},
_getAtlas (isFirst) {
axios.get(this, `/v1/atlas/${this.area.id}/yes`, null, (data) => {
let tmp = []
data.forEach(item => {
let w200 = null
if (item.indexOf(QINIU_PREFIX) > -1) {
w200 = item + '-w200'
}
tmp.push({
src: item,
w200: w200
})
})
if (isFirst) {
this.localImgs = tmp
} else {
this.atlas = tmp
}
})
},
_getMessageRecord() {
const userId = localUser.get('id')
if (userId == null || userId === '') {
return
}
axios.get(this, `/v1/messageRecords/${userId}`, {
endTime: this.moment().subtract(7, 'days').format('YYYY-MM-DD'),
pageSize: 50
}, (data) => {
let today = []
let nearlyThree = []
let lastDays = []
data.forEach(item => {
if (this.moment(item.updateAt).format('YYYY-MM-DD') === this.moment().format('YYYY-MM-DD')) {
today.push(item)
} else if (this.moment(item.updateAt).isAfter(this.moment().subtract(3, 'days').format('YYYY-MM-DD'))) {
nearlyThree.push(item)
} else {
lastDays.push(item)
}
})
this.messageRecord = [
today,
nearlyThree,
lastDays
]
})
},
scroll (position, movingY) {
if (position.y <= -230) {
this.opacity = 1
} else {
this.opacity = Math.abs(position.y / 230)
}
},
startMarquee (time) {
this.stopMarquee()
this.marqueeTimer = setInterval(() => {
if (this.activeMarqueeIndex < (this.hostDynamic.length - 4)) {
this.activeMarqueeIndex ++
this.marqueeAnimate = true
} else {
this.activeMarqueeIndex = 0
this.marqueeAnimate = false
}
}, time)
},
stopMarquee () {
if (this.marqueeTimer) {
clearInterval(this.marqueeTimer)
this.marqueeTimer = null
}
},
toJingdian () {
this.$router.push(`/cart/productList/1`)
},
toJiudian () {
this.$router.push(`/cart/productList/0`)
},
toMeiShi () {
this.$router.push(`/address/food`)
},
toGonglue () {
this.$router.push('/address/strategy')
},
toYouJi () {
this.$store.commit('SET_MEITU_TAB', 2)
this.$router.push('/meiTu')
},
toWenDa () {
this.$vux.toast.text('待开发~', 'middle')
},
toMap () {
this.$router.push(`/address/map/${this.area.id}`)
},
toRecommend () {
this.$vux.toast.text('待开发~', 'middle')
},
toProductDetail (id, type) {
this.$router.push(`/address/product/${id}/${type}`)
},
toDynamicDesc (item) {
this.$router.push(`/meiTu/dynamicDesc/${item.id}`)
},
toAtlas () {
this.showAtlas = true
this._getAtlas()
},
submitSearch (value) {
axios.get(this, `/v1/product`, {
name: value,
type: this.area.type,
pageSize: 10
}, (data) => {
this.searchList = data
})
},
clickSearchItem (item) {
this.toProductDetail(item.id, item.type)
this.showSearch = false
this.seachValue = ''
this.searchList = []
},
clickRecordItem (id, type) {
this.toProductDetail(id, type)
this.showLasted = false
},
toUSerDetail (userId) {
this.$router.push(`/meiTu/userDynamic/${userId}`)
},
toAmusement (categoryId) {
this.$router.push(`/address/amusement/${this.area.id}/${categoryId}`)
},
toLasted () {
this.showLasted = true
this._getMessageRecord()
},
toContent() {
this.$router.push(`/content/${this.area.contentId}/${'本地概况-' + this.area.name}`)
},
toNative() {
this.$store.commit('SET_MEITU_TAB', 2)
this.$router.replace('/meiTu')
},
selectArea (area) {
this.setCurrentArea(area)
this.showSelectAddress = false
this.area = area
},
...mapMutations({
setCurrentArea: 'SET_CURRENT_AREA'
})
},
watch: {
recommendRate (newValue, oldValue) {
var vm = this
function animate () {
if (TWEEN.update()) {
requestAnimationFrame(animate)
}
}
const tmp = { tweeningNumber: oldValue }
new TWEEN.Tween(tmp)
.easing(TWEEN.Easing.Quadratic.Out)
.to({ tweeningNumber: newValue }, 1800)
.onUpdate(function () {
vm.recommendRateCopy = tmp.tweeningNumber.toFixed(2)
})
.start()
animate()
},
currentArea (newVal) {
if (newVal != null && newVal.isRefresh !== true) {
if (typeof newVal === 'string') {
this._getAreaInfo(newVal)
} else {
this._getAreaInfo(newVal.id)
}
}
}
}
}



