
import Scroll from '@/components/scroll/scroll'
import productImg from '@/assets/img/specialLocal-product.png'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


export default {
data () {
return {
productImg: productImg,
productName: '特产名'
}
},
components: {
Scroll,
Grid,
GridItem
},
mounted () {
this.$refs.verticalScroll.$el.style.height = (window.innerHeight - 130) + 'px'
},
methods: {
specialDetail () {
this.$router.push(`/specialLocalProductDetail`)
},
upperLevel (v) {
if (v === 1) {
this.show = false
}
}
}
}



