
import VHeader from '@/components/v-header/v-header'
import SearchHeader from '@/components/seach-header/seach-header'
import Scroll from '@/components/scroll/scroll'
import Slider from '@/components/slider/slider'
import IndexList from '@/components/index-list/index-list'
import Range from '@/components/range/range'
import Rater from 'vux/src/components/rater/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import { mapGetters, mapMutations } from 'vuex'

import { getDistance } from '@/assets/js/amap'
import nativeBridge from '@/assets/js/nativeBridge'
import axios from '@/api/axiosApi'

import icon from '@/assets/json/addressIcon.json'
import { isIphoneX } from '@/assets/js/brower'

export default {
name: 'ProductList',
props: {
type: {
type: Number,
default: 0
}
},
data () {
return {
icon,
bannerList: [],
headerOpacity: 0,
currentPosition: [],
hotProduct: [],
hotTags: [{
id: '',
name: '全部'
}],
currentTag: '',
productList: [],
showSearch: false,
seachValue: '',
searchList: [],
addressList: [],

addressInfo: {
id: '',
name: '目的地'
},
priceSort: {
between: [0, 3000],
level: []
},
tempPriceSort: {
between: [0, 3000],
level: []
},
fitCount: 0,  // 符合条件的数量
otherSort: {
value: 'rate',
type: 'DESC',
label: '综合排序'
},
sortType: [
{
value: 'rate',
type: 'DESC',
label: '综合排序'
},
{
value: 'sellSum',
type: 'DESC',
label: '销量优先'
},
{
value: 'avgPrice',
type: 'ASC',
label: '价格低→高'
},
{
value: 'avgPrice',
type: 'DESC',
label: '价格高→低'
},
{
value: 'distance',
label: '离我最近'
}
],
hotelLevel: [
{
value: 5,
label: '五星/豪华'
},
{
value: 4,
label: '四星/高端'
},
{
value: 3,
label: '三星/舒适'
},
{
value: 2,
label: '一般/经济'
}
],
scenicLevel: [
{
value: 5,
label: '5A级'
},
{
value: 4,
label: '4A级'
},
{
value: 3,
label: '3A级'
},
{
value: '',
label: '不限'
}
],
showSelectAddress: false,
showSort: false,
showPriceLevel: false,
addressId: 'ba062c32fdf611e7ba2d00163e0c27f8'
}
},
directives: {
TransferDom
},
computed: {
...mapGetters([
'currentArea'
])
},
components: {
VHeader, Scroll, Rater, Popup, Slider, Grid, GridItem, Scroller, IndexList, Range, Checker, CheckerItem, SearchHeader
},
mounted () {

this.$refs.scroll.$el.style.height = (window.innerHeight - this.$refs.scroll.$el.offsetTop) + 'px'
},
created () {
this.paddingBottom = 0
if (isIphoneX()) {
this.paddingBottom = 20
}
this._getBanner()
this._getAddressInfo()
this._getProductList()
this._getAddressList()
this._getHotProduct()
this._getHotTags()
},
watch: {
type () {
this._getBanner()
this._getAddressInfo()
this._getProductList()
this._getAddressList()
this._getHotProduct()
this._getHotTags()

}
},
methods: {
back () {
this.$router.go(-1)
},
_getBanner () {

axios.get(this, `/v1/banner/${this.type === 0 ? 2 : 1}`, null, (data) => {

data.forEach(item => {
item.thumbUrl += '-banner'
})
this.bannerList = data
}, null, false)
},
_getAddressList () {
axios.get(this, `/v1/address/1`, {
pageSize: 50
}, (data) => {
let temp = []
data.forEach(item => {
const findIndex = temp.findIndex((o) => o.name === item.nameSpell.toUpperCase()[0])
if (findIndex < 0) {
temp.push({
name: item.nameSpell.toUpperCase()[0],
items: [item]
})
} else {
temp[findIndex].items.push(item)
}
})
temp.sort((o1, o2) => o1.name.charCodeAt(0) - o2.name.charCodeAt(0))
this.addressList = temp
})
},
_getAddressInfo () {
axios.get(this, `/v1/addressInfo/${this.currentArea.id}`, null, (data) => {

this.addressInfo = data
})
},
_getHotProduct () {
axios.get(this, `/v1/product/${this.currentArea.id}/${this.type}`, {
recommend: 1,
pageSize: 8
}, (data) => {

this.hotProduct = data
})
},
_getProductList () {
const _self = this
axios.get(this, `/v1/product/${this.currentArea.id}/${this.type}`, {
sortName: this.otherSort.value,
sortType: this.otherSort.type,
level: this.priceSort.level.join(''),
minAvgPrice: this.priceSort.between[0],
maxAvgPrice: this.priceSort.between[1] >= 3000 ? '' : this.priceSort.between[1],
tagIds: this.currentTag
}, (data) => {
data.forEach(item => {
item.distance = 0
item.distanceStr = ''
})
_self.productList = data
nativeBridge.getPosition(_self, (arg) => {
_self.currentPosition = [parseFloat(arg.longitude), parseFloat(arg.latitude)]
data.forEach(item => {
item.distance = getDistance(_self.currentPosition, [item.longitude, item.latitude])
item.distanceStr = _self.distanceStr(getDistance(_self.currentPosition, [item.longitude, item.latitude]))
})
_self.productList = data
})
})
},
_getHotTags () {
axios.get(this, `/v1/tag`, {
type: this.type === 0 ? 101 : 102,
pageSize: 12
}, (data) => {


data.splice(0, 0, {
id: '',
name: '全部'
})
this.hotTags = data
if (data.length > 0) {
this.currentTag = data[0].id
}
setTimeout(() => {
let w = 0
const eles = this.$refs.tagWrapper.children
for (let i = 0; i < eles.length; i++) {
const item = eles[i]
w += item.offsetWidth + 12
}
this.$refs.tagWrapper.style.width = w + 'px'
}, 50)
})
},
_getProductNum () {
axios.get(this, `/v1/productCount/${this.type}/${this.tempPriceSort.between[0]}/${this.tempPriceSort.between[1]}`, {
level: this.tempPriceSort.level.join('')
}, (data) => {
this.fitCount = data
})
},
scroll (position) {
if (position.y <= -128) {
this.headerOpacity = 1
} else {
this.headerOpacity = Math.abs(position.y / 188)
}
},
distanceStr (distance) {
if (distance < 200) {
return '200米以内'
} else if (distance < 1000) {
return '1Km范围以内'
} else if (distance < 2 * 1000) {
return '2Km范围以内'
} else if (distance < 200 * 1000) {
return '距离' + (distance / 1000).toFixed(1) + 'Km'
} else {
return '超过200Km'
}
},
colllect (id) {
this.productList.forEach(item => {
if (item.id === id) {
item.collectionStatus = item.collectionStatus === 0 ? 1 : 0
}
})
},
clickBanner (banner) {
if (banner.type === 0 && banner.contentId) {
this.$router.push(`/content/${banner.contentId}/${banner.name}`)
} else if (banner.type !== 0 && banner.linkId) {
this.$router.push(`/address/product/${banner.linkId}/${this.type}`)
}
},
submitSearch (value) {
axios.get(this, `/v1/product`, {
areaId: this.$route.params.addressId,
type: this.type,
name: value,
pageSize: 10
}, (data) => {
this.searchList = data
})
},
clickSearchItem (item) {
this.clickProductItem(item.id, this.type)
this.showSearch = false
this.seachValue = ''
this.searchList = []
},
clickProductItem (id, type) {
this.$router.push(`/cart/product/${id}/${type}`)
},
clickTag (tagId) {
this.currentTag = tagId
this._getProductList()
},
selectArea (area) {
this.$router.replace(`/cart/product/${area.id}/${this.type}`)
this.setCurrentArea(area.id)
this.showSelectAddress = false
this.addressInfo = area
},
clickSortType (sortItem) {
if (sortItem.value === 'distance') {
this.productList.sort((o1, o2) => o1.distance > o2.distance)
} else {
this.otherSort = sortItem
this._getProductList()
}
this.showSort = false
},
filterPriceResult () {
this.priceSort = this.tempPriceSort
this._getProductList()
this.showPriceLevel = false
},
toShowPriceLevel () {
this.showPriceLevel = true
this.tempPriceSort = {
between: this.priceSort.between,
level: this.priceSort.level
}
},
resetPriceLevel () {

this.tempPriceSort = {
between: [0, 3000],
level: []
}
},
priceChange (val) {
this.tempPriceSort.between = val
this._getProductNum()
},
...mapMutations({
setCurrentArea: 'SET_CURRENT_AREA'
})
}
}



